import React from 'react'
import styled from 'styled-components'
import { Marginer } from '../marginer'; 

const CardContainer = styled.div`
    display:flex;
    flex-direction:column;
    width:300px;
    min-height:258px;
    background-color:#fff;
    box-shadow:0 0 3px rgba(0,0,0,0.4);
    margin:0 0.5em;
    margin-bottom:1.3em;
`;

const TopContainer = styled.div`
    width:100%;
`;

const ServiceThumbnail = styled.div`
    width:100%;
    height:11em;

    img{
        width:100%;
        height:100%;
    }
`;


const ContentContainer  = styled.div`
    width:100%;
    display:flex;
    flex-direction:column; 
    justify-content:center;
    padding: 15px 10px; 
    align-items:flex-start;
`;


const BottomContainer = styled.div`
 width:100%;
 height:32px;
 display:flex;
 align-items:center;
 justify-content:space-between;
 border-top:1px solid rgba(15,15,15,0.17);
 padding:0 10px;
`;

const Title = styled.h2`
    font-size:18px;
    margin:0;
    font-weight:500;
    color:#000;
    justify-content:flext-start;
`;

const SpecialistNAme =styled.h4`
    margin:0;                                     
    color:rgba(150,151,151,1);
    font-size:12px;
    font-weight:400;
`;

const RatingContainer = styled.div`
    display:flex;
    color:#EBE204;
`;

const StarttingAtText = styled.h6`
    margin:0;
    color:rgba(161,161,161,0.9);
    font-weight:400;
`;

const PriceContainer = styled.div`
     display:flex;
     align-items:center;
`;

const PriceText = styled.div`
    margin-left:3px;
    color:#2A9D8F;
    font-weight:500;
`;


export function ServiceCard(props) {
    const {thumbnailUrl,specialist,id,title,rate,rating} = props;
    return (
        <CardContainer>
            <TopContainer>
                <ServiceThumbnail>
                    <img src ={thumbnailUrl} alt ={title}/>
                </ServiceThumbnail>
            </TopContainer>
            <ContentContainer>
                <Title> {title} </Title>
                <Marginer  direction ='vertical' margin={10}/>
                <SpecialistNAme>{specialist.fullName}</SpecialistNAme>
            </ContentContainer>
            <BottomContainer>
                <RatingContainer>
                      Star Rating: {rating}
                </RatingContainer>
                <PriceContainer>
                    <StarttingAtText> Starting At :</StarttingAtText>
                    <PriceText>${rate}/hr</PriceText>
                </PriceContainer>
            </BottomContainer>
        </CardContainer>
    ) 
}

